<template>
  <div class="mdphone container">
    <router-link to="/user/account" tag="div" class="mdphone-user">&lt;{{ $t('navbar.user.navItem1') }}</router-link>
    
    <div id="" class="content-title">{{ $t('tradePwd.part5') }}</div>
      <steps-bar 
        :index="active"
        bgColor="#0bc988"
        disableColor="#e4e4ea"
        activeColor="#0bc988"
        class="steps-bar">
      </steps-bar>
      <el-form :model="form" :rules="formRules" ref="form" label-width="150px" v-show="active == 1" class="mdphone-form">
        <el-form-item :label="$t('form.phone')">
          <div>{{tel}}</div>
        </el-form-item>
        <el-form-item :label="$t('form.verifyCode')" prop="sms_verify" class="verify">
          <el-input type="text" auto-complete="off" :placeholder="$t('form.verifyCodePlaceholder')" v-model.trim="form.sms_verify"></el-input><el-button class="get-verify" @click="handleSendRequestSms($event)" :disabled="!validCode">{{ validTip }}</el-button>
        </el-form-item>
        <el-form-item :label="$t('centification.userInfo3')" prop="identity_number">
          <el-input v-model.trim="form.identity_number" :placeholder="$t('centification.message3')"></el-input>
        </el-form-item>
        <div class="btn-group appeal-btn">
          <el-button @click="nextSubmit('form')" type="primary" >{{ $t('tradePwd.part8') }}</el-button>
        </div>
      </el-form>

      <el-form :model="resetForm" :rules="resetRules" ref="resetForm" label-width="150px" v-show="active == 2" class="mdphone-form">
        <el-form-item :label="$t('tradePwd.part11')" prop="new_password">
          <el-input v-model.trim="resetForm.new_password" type="password" :placeholder="$t('tradePwd.part12')"></el-input>
        </el-form-item>
        <el-form-item :label="$t('tradePwd.part13')" prop="new_confirmpwd">
          <el-input v-model.trim="resetForm.new_confirmpwd" type="password" :placeholder="$t('tradePwd.part14')"></el-input>
        </el-form-item>
        <div class="btn-group appeal-btn">
          <el-button type="primary"  @click="resetSubmit('resetForm')" style="margin-left:150px;">{{ $t('dialog.submit') }}</el-button>
        </div>
      </el-form>

      <div class="c-green centify-success" v-if="active == 3"><img src="static/images/right.png" alt="">{{ $t('tradePwd.part10') }}</div>
      
    </div>
</template>

<script>
  import StepsBar from '@/components/StepsBar'
  import Paybox from '@/components/Paybox'

  import { sendRequestSms } from '@/api/loginModule'
  import { checkUserAuth, resetPwdTrade } from '@/api/user'
  import { sixNumber, tradePwd } from '@/utils/validate'

  import md5 from 'js-md5'
  export default {
    components: { StepsBar, Paybox },
    data() {
      // const validatePass = (rule, value, callback) => {
      //   if (value === '') {
      //     callback(new Error(this.$t('errorTips.withoutPwd')))
      //   } else {
      //     if (!sixNumber(value)) {
      //       // 错误提示
      //       callback(new Error(this.$t('errorTips.tradePwdError')))
      //     }
      //     callback()
      //   }
      // }
      const validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(this.$t('errorTips.surePwd')))
        } else if (value !== this.resetForm.new_password) {
          callback(new Error(this.$t('errorTips.surePwdError')))
        } else {
          callback()
        }
      }
      const validatePass3 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(this.$t('errorTips.withoutPwd')))
        } else {
          if (!sixNumber(value)) {
            // 错误提示
            callback(new Error(this.$t('errorTips.tradePwdError')))
          }
          if (!tradePwd(value)) {
            // 错误提示
            callback(new Error(this.$t('tradePwd.part15')))
          }
          callback()
        }
      }
      return {
        active: 1,
        resetForm: {
          new_password: '',
          new_confirmpwd: ''
        },
        link_userInfo: null,
        resetRules: {
          new_password: [
            { validator: validatePass3, trigger: 'blur' }
            // { required: true, message: this.$t('errorTips.withoutPwd'), trigger: 'blur' }
          ],
          new_confirmpwd: [
            { validator: validatePass2, trigger: 'blur' }
            // { required: true, message: this.$t('errorTips.tradePwdError'), trigger: 'blur' }
          ]
        },
        form: {
          sms_verify: '',
          identity_number: ''
        },
        formRules: {
          sms_verify: [
            { required: true, message: this.$t('errorTips.withoutVerifyCode'), trigger: 'blur' }
          ],
          identity_number: [
            { required: true, message: this.$t('tradePwd.withoutNumber'), trigger: 'blur' }
          ]
        },
        validCode: true,
        validTip: this.$t('verifyCode.title')
      }
    },
    computed: {
      tel: function() {
        let link_userInfo = window.localStorage.getItem('link_userInfo')
        link_userInfo = JSON.parse(link_userInfo)
  
        return link_userInfo.phone.substr(0, 3) + '***' + link_userInfo.phone.substr(-4, 4)
      }
    },
    created() {
  
    },
    methods: {
      nextSubmit(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            const data = { ...this.form }
            data.check_options = 2
            checkUserAuth(data).then(res => {
              if (res.status !== 1000) {
                this.$message.error(res.msg)
                return false
              }

              this.active++
            }).catch(() => {
            })
          } else {
            console.log('error submit!!')
            return false
          }
        })
      },
      handleCloseDialog() {
        this.$emit('closeDialog', 'centifyByNumberDialogVisible')
      },
      resetSubmit(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            const data = { ...this.resetForm }
            data.new_password = md5(data.new_password)
            data.new_confirmpwd = md5(data.new_confirmpwd)
            resetPwdTrade(data).then(res => {
              if (res.status !== 1000) {
                this.$message.error(res.msg)
                return false
              }
              this.active++
              // this.$message.success(this.$t('user.safeCenfity.tips.part5'))
              // this.handleCloseDialog()
            }).catch(() => {
            })
          } else {
            console.log('error submit!!')
            return false
          }
        })
      },
      handleSendRequestSms(event) {
        const data = {}
        this.setTime()
        this.link_userInfo = window.localStorage.getItem('link_userInfo')
        this.link_userInfo = JSON.parse(this.link_userInfo)
        data.user_account = this.link_userInfo.phone
        data.request_type = 'auth'
        sendRequestSms(data).then(res => {
          console.log('res', res)
          if (res.status !== 1000) {
            this.$message.error(res.msg)
            return false
          }
        }).catch(() => {
        })
      },
      setTime() {
        let time = 60
        if (this.validCode) {
          this.validCode = false
          // const _this = this
          const t = setInterval(() => {
            time--
            this.validTip = time + this.$t('verifyCode.second')
            if (time === 0) {
              clearInterval(t)
              this.validTip = this.$t('verifyCode.getAgain')
              this.validCode = true
            }
          }, 1000)
          return t
        }
      }
    }
  }
</script>

<style lang="less" scoped>
  .mdphone{
    background: white;
    padding: 20px 25px;
    font-size: 14px;
    min-height: 700px;
    .mdphone-user{
      margin-bottom: 15px;
      color: #1fa8f6;
      font-size: 16px;
      cursor: pointer;
    }
    .payway{
      margin-bottom: 15px;
      label{
        display: inline-block;
        text-align: right;
        padding-right: 10px;
      }
    }
    .steps-bar{
      padding: 60px 0 40px;
      width: 580px;
      margin: 0 auto;
      font-size: 14px;
    }
    .mdphone-form{
      width: 440px;
      margin: 0 auto;
      /deep/.el-button--primary{
        margin-left: 150px;
        width: 290px;
      }
    }
    
    .old-pwd{
      margin-left: -60px;
      margin-top: 30px;
    }
    
    .code-tips{
      margin-top: 89px;
      color: #b4b6be;
      font-size: 12px;
    }
    .centify-success{
      font-size: 18px;
      font-weight: bold;
      margin-left: 100px;
      margin-top: 30px;
      line-height: 43px;
      width: 400px;
      margin: 0 auto;
      text-align: center;
      img{
        display: inline-block;
        margin-right: 20px;
      }
    }
    .tips{
      color: #b0b1b7;
      margin-left: 90px;
      margin-bottom: 10px;
    }
  .content-title {
    padding-top: 9px;
    padding-bottom: 18px;
    border-bottom: 1px solid #e6eaf0;
    font-size: 16px;
  }
  }
</style>
<style lang="less">
  .mdphone{
    .el-step__title.is-success{
      color: #02c56c !important;
    }
    .el-step__head.is-success{
      color: #02c56c;
      border-color: #02c56c;
    }
    .avatar-uploader{
      width: 109px;
      display: inline-block;
    }
    .avatar-uploader .el-upload {
      border: 1px dashed #d9d9d9;
      border-radius: 6px;
      cursor: pointer;
      position: relative;
      overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
      border-color: #409EFF;
    }
    .avatar-uploader-icon {
      font-size: 28px;
      color: #8c939d;
      width: 109px;
      height: 109px;
      line-height: 109px;
      text-align: center;
    }
    
    .avatar {
      width: 109px;
      height: 109px;
      display: block;
    }

  }
</style>
<style lang="less">
  .mdphone{
    .verify .el-input__inner,
    .verify .el-input{
      width: 184px;
    }
    .get-verify {
      width: 95px;
      margin-left: 10px;
      display: inline-block;
      padding: 12px 13px;
      background: rgba(31,168,246,.15);
      color: rgba(31, 168, 246, 0.7);
    }
    .sure-change{
      padding: 12px 100px;
    }
    .el-step__title{
      font-size: 14px;
    }
  }
</style>


